.bg-image {

    background-image: url("../assets/images/bg3.jpg");

    /* The image used */
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#d6226a+0,1fc1df+100 */
    /*background: rgb(214,34,106); !* Old browsers *!*/
    /*background: -moz-linear-gradient(-45deg,  rgba(214,34,106,1) 0%, rgba(31,193,223,1) 100%); !* FF3.6-15 *!*/
    /*background: -webkit-linear-gradient(-45deg,  rgba(214,34,106,1) 0%,rgba(31,193,223,1) 100%); !* Chrome10-25,Safari5.1-6 *!*/
    /*background: linear-gradient(135deg,  rgba(214,34,106,1) 0%,rgba(31,193,223,1) 100%); !* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ *!*/
    /*filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#d6226a', endColorstr='#1fc1df',GradientType=1 ); !* IE6-9 fallback on horizontal gradient *!*/

    /*!* Add the blur effect *!*/
    filter: blur(8px) grayscale(0%);
    -webkit-filter: blur(8px) grayscale(0%);

    /* Center and scale the image nicely */
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    z-index: 100;
    height: 100vh;
}

.bg-text {
    color: white;
    font-weight: bold;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1000;
    width: 100%;
    text-align: center;
    margin-top: 100px;
}

